table.mapSchedule {
  width: 100%;
  min-width: 300px;
  max-width: calc(100% - 24px);
  margin: 12px 12px 0 12px;
  border: 1px solid #ccc;
  padding: 0;
  border-collapse: collapse;
}

table.mapSchedule td {
  padding: 0;
  vertical-align: top;
  font-size: 80%;
  width: 14.2857142857%;
  text-align: center;
}

table.mapSchedule td:nth-child(even) {
  background: #f5f5f5;
}

table.mapSchedule td {
  position: relative;
  vertical-align: top;
  text-align: center;
  padding-top: 19px;
}

table.mapSchedule td[day=ПН]::before,
table.mapSchedule td[day=ВТ]::before,
table.mapSchedule td[day=СР]::before,
table.mapSchedule td[day=ЧТ]::before,
table.mapSchedule td[day=ПТ]::before,
table.mapSchedule td[day=СБ]::before,
table.mapSchedule td[day=ВС]::before {
  background: #3887be;
  position: absolute;
  top: 0px;
  left: 0;
  z-index: 1;
  display: block;
  text-align: center;
  content: "";
  color: #fff;
  width: 100%;
  height: 19px;
}

table.mapSchedule td[day=ПН]::before {
  content: "ПН";
}

table.mapSchedule td[day=ВТ]::before {
  content: "ВТ";
  opacity: 0.8;
}

table.mapSchedule td[day=СР]::before {
  content: "СР";
}

table.mapSchedule td[day=ЧТ]::before {
  content: "ЧТ";
  opacity: 0.8;
}

table.mapSchedule td[day=ПТ]::before {
  content: "ПТ";
}

table.mapSchedule td[day=СБ]::before {
  content: "СБ";
  opacity: 0.8;
}

table.mapSchedule td[day=ВС]::before {
  content: "ВС";
}

table.mapSchedule td.today_td {
  background: #ecffeb;
  font-weight: 600;
}

table.mapSchedule td.today_td::before {
  font-weight: 800;
}

