.group__item {
  display: flex;
  justify-content: flex-start;
  align-items: flex-start;
}
.group__item label {
  color: gray;
  font-size: 70%;
  display: block;
  line-height: 1em;
  margin-bottom: 3px;
}
.group__item-new {
  /* Permalink - use to edit and share this gradient: https://colorzilla.com/gradient-editor/#cbffa9+0,ff8989+100&1+0,0+16,0+65 */
  background: linear-gradient(to right, rgb(203, 255, 169) 0%, rgba(211, 236, 164, 0) 16%, rgba(237, 178, 148, 0) 65%, rgba(255, 137, 137, 0) 100%);
  /* W3C, IE10+, FF16+, Chrome26+, Opera12+, Safari7+ */
  padding-left: 24px;
  border-radius: 4px;
}
.group__form {
  width: 350px;
  margin-right: 60px;
}
.group__form > div {
  margin-bottom: 6px;
}
.group__preview {
  position: relative;
  width: 450px;
}
.group__preview .dayTime {
  margin-bottom: 8px;
  justify-content: flex-start;
  align-items: center;
}
.group__preview .dayTime span {
  display: inline-block;
  margin-right: 8px;
  width: 24px;
  border-radius: 50%;
  height: 24px;
  font-size: 80%;
  color: #fff;
  line-height: 24px;
  text-align: center;
  background: #00A9FF;
}
.group__preview .dayTime.dayHoly span {
  background: pink;
}
.group__preview .dayTime input {
  max-width: 250px;
}
.group__preview td[day] {
  min-width: 60px;
}
.group__preview td[day] div {
  white-space: nowrap;
  font-size: 90%;
  text-align: center;
}

.deleteGroup {
  position: absolute;
  right: 0;
  top: 0;
}
@charset "UTF-8";
.show-all-array {
  line-height: 0;
  min-height: 360px;
}

.show-all-array li {
  line-height: 1em;
}

.mapMetro,
.mapTown,
.mapAdress,
.mapPhone,
.mapEmail,
.mapSite,
.mapTypeMed,
.mapTypeSpecial,
.mapWarning,
.mapDescr {
  display: block;
  position: relative;
  margin-left: 12px;
  padding-right: 12px;
  padding-left: 17px;
  font-size: 14px;
}

.mapAdress {
  background: url(./Assets/Img/icon-mapmarker.png) left 1px no-repeat;
  background-size: 10px 12px;
  line-height: 1.05em;
  margin-top: 4px;
  margin-bottom: 4px;
  font-size: 14px;
}

.mapMetro {
  background: url(./Assets/Img/icon-metro.png) left 6px no-repeat;
  background-size: 10px auto;
  font-size: 14px;
}

.mapPhone {
  background: url(./Assets/Img/icon-phone.png) left 5px no-repeat;
  background-size: 10px auto;
  padding-right: 0;
  display: inline-block;
  font-size: 14px;
}

.mapEmail {
  background: url(./Assets/Img/icon-email.png) left 5px no-repeat;
  background-size: 12px auto;
}

.mapWarning {
  background: url(./Assets/Img/warning-icon.svg) left 5px no-repeat;
  background-size: 10px auto;
  color: red;
  font-size: 14px;
  margin-top: 12px;
}

.mapDescr {
  background: url(./Assets/Img/icon-descr.svg) left 0 no-repeat;
  background-size: 13px auto;
  line-height: 1.05em;
  font-size: 14px;
  margin-top: 12px;
}

.mapTown {
  background: url(./Assets/Img/icon-town.svg) -2px 2px no-repeat;
  background-size: 14px auto;
}

.mapSite {
  background: url(./Assets/Img/icon-site.png) left 5px no-repeat;
  background-size: 12px auto;
}

.mapTypeMed {
  margin-bottom: 4px;
  border: 1px solid #3887be;
  border-radius: 2px;
  color: #004785;
  padding: 0 6px 0 20px;
  font-size: 10px;
  display: inline-block;
  background: url(./Assets/Img/medicine-icon.svg) 5px center no-repeat;
  background-size: 10px auto;
}

.mapTypeSpecial {
  margin-top: 12px;
  font-weight: 600;
}

.mapTypeSpecial::before {
  position: relative;
  display: inline-block;
  content: "Специализированная группа";
}

