table.mapSchedule {
  width: 100%;
  min-width: 300px;
  max-width: calc(100% - 24px);
  margin: 12px 12px 0 12px;
  border: 1px solid #ccc;
  padding: 0;
  border-collapse: collapse;
}

table.mapSchedule td {
  padding: 0;
  vertical-align: top;
  font-size: 80%;
  width: 14.2857142857%;
  text-align: center;
}

table.mapSchedule td:nth-child(even) {
  background: #f5f5f5;
}

table.mapSchedule td {
  position: relative;
  vertical-align: top;
  text-align: center;
  padding-top: 19px;
}

table.mapSchedule td[day=ПН]::before,
table.mapSchedule td[day=ВТ]::before,
table.mapSchedule td[day=СР]::before,
table.mapSchedule td[day=ЧТ]::before,
table.mapSchedule td[day=ПТ]::before,
table.mapSchedule td[day=СБ]::before,
table.mapSchedule td[day=ВС]::before {
  background: #3887be;
  position: absolute;
  top: 0px;
  left: 0;
  z-index: 1;
  display: block;
  text-align: center;
  content: "";
  color: #fff;
  width: 100%;
  height: 19px;
}

table.mapSchedule td[day=ПН]::before {
  content: "ПН";
}

table.mapSchedule td[day=ВТ]::before {
  content: "ВТ";
  opacity: 0.8;
}

table.mapSchedule td[day=СР]::before {
  content: "СР";
}

table.mapSchedule td[day=ЧТ]::before {
  content: "ЧТ";
  opacity: 0.8;
}

table.mapSchedule td[day=ПТ]::before {
  content: "ПТ";
}

table.mapSchedule td[day=СБ]::before {
  content: "СБ";
  opacity: 0.8;
}

table.mapSchedule td[day=ВС]::before {
  content: "ВС";
}

table.mapSchedule td.today_td {
  background: #ecffeb;
  font-weight: 600;
}

table.mapSchedule td.today_td::before {
  font-weight: 800;
}

span.mapGroupName,
span.mapGroupNameInner {
  font-size: 18px;
  font-weight: bold;
}

span.mapGroupName {
  font-weight: 600;
  padding: 6px 10px;
  color: #3887be;
  margin: 0;
}

span.mapGroupNameInner {
  position: relative;
  margin-bottom: 6px;
  color: #fff;
  display: inline-block;
  width: 100%;
  font-weight: 600;
  margin-left: 0px;
  padding: 10px 12px;
  background: #3887be;
}

ymaps.ymaps-2-1-79-balloon__content {
  padding-left: 0 !important;
  padding-top: 0 !important;
  margin-right: 0 !important;
  font-family: "Montserrat", sans-serif;
}

ymaps[class$=balloon__close-button] {
  background: unset;
  transition: 0.3s;
  opacity: 1;
}

ymaps[class$=balloon__close-button]::before {
  position: absolute;
  top: 0;
  right: 0;
  display: flex;
  align-items: center;
  justify-content: center;
  color: #fff;
  content: "+";
  width: 30px;
  height: 30px;
  transform: rotate(-45deg);
  font-size: 22px;
}

ymaps.ymaps-2-1-79-balloon-pane {
  transform: scale(1, 1) !important;
}

.show-all-array > b,
.schedule-map > b,
.show-all-array > br,
.schedule-map > br {
  display: none !important;
}

.schedule-map {
  margin-top: 40px;
}

.schedule-map + footer.footer {
  margin-top: 0;
}

.show-all-array,
.schedule-map {
  font-size: 0;
}

.show-all-array > *,
.schedule-map > * {
  font-size: 1rem;
}

