.show-all-array li,
.schedule-map li {
  list-style-type: none;
  overflow: hidden;
  box-shadow: 0 0 5px 0 rgba(0, 0, 0, 0.1);
  /*border-right: 4px solid #dbdada;*/
  background: #fff;
}

.show-all-array > li {
  padding: 0 0 12px;
  font-size: 1rem;
  line-height: 1.3em;
  display: flex;
  align-items: flex-start;
  justify-content: space-between;
  flex-wrap: wrap;
  margin: 0 0 24px;
}

/* .show-all-array > li:last-of-type {
    margin: 0;
