.show-all-array li > div {
  flex-basis: 100%;
}

.show-all-array .mapGroupNameInner {
  /* background: rgba(28,49,125,1); */
  color: #004785;
  /* #3887be; */
  background: none transparent;
}

.groupCtrl {
  position: relative;
  /*margin-bottom: 6px;*/
  display: block;
  padding: 10px 12px 0;
  font-size: 80%;
  display: flex;
  justify-content: flex-end;
  align-items: center;
  color: #3887be;
}

.show-all-array__view {
  padding: 12px;
  display: block;
  background: gray;
  margin-bottom: 30px;
  color: #fff;
}

.show-all-array__view span {
  cursor: pointer;
}

.show-all-array__view span.active {
  text-decoration: underline;
  font-weight: 600;
}

.groupCtrl .toggle-map i,
.groupCtrl .toggle-fav i {
  margin-right: 6px;
  color: inherit;
}

.groupCtrl .toggle-fav,
.groupCtrl .toggle-map {
  text-decoration: none;
  cursor: pointer;
}

.groupCtrl .toggle-fav:hover,
.groupCtrl .toggle-map:hover {
  text-decoration: underline;
  color: #004785;
}

.groupCtrl .toggle-map {
  margin-right: 24px;
}

.show-all-array__filter {
  width: 100%;
  font-size: 14px;
  display: flex;
  justify-content: space-between;
  width: 100%;
  margin-bottom: 30px;
  /* background: #fff;*/
}

.show-all-array__filter-group {
  /* padding: 12px;
  flex-basis: 33.33%;*/
  flex-basis: 100%;
  display: flex;
  justify-content: flex-start;
}

.show-all-array__option-header {
  margin-bottom: 10px;
  display: none;
  /*text-decoration: underline;*/
  font-size: 80%;
  /*color: gray;*/
}

.show-all-array__option {
  margin-bottom: 10px;
  margin-right: 40px;
}

.group-counter {
  position: absolute;
  right: 12px;
  top: 12px;
  width: 50px;
  height: 20px;
  display: inline-block;
  padding-left: 16px;
  background: url(./Assets/Img/filter-icon.svg) left 5px no-repeat;
  background-size: 13px 11px;
}

.group-counter {
  filter: grayscale(100%);
}

.show-all-array {
  display: flex;
  flex-direction: column;
  padding: 0;
}

.show-all-array li {
  order: 1;
}

li.closed table.mapSchedule td[day=ПН]::before,
li.closed table.mapSchedule td[day=ВТ]::before,
li.closed table.mapSchedule td[day=СР]::before,
li.closed table.mapSchedule td[day=ЧТ]::before,
li.closed table.mapSchedule td[day=ПТ]::before,
li.closed table.mapSchedule td[day=СБ]::before,
li.closed table.mapSchedule td[day=ВС]::before {
  background-color: rgba(177, 38, 21, 0.59);
}

table.mapSchedule td[day=ПН]::before,
table.mapSchedule td[day=ВТ]::before,
table.mapSchedule td[day=СР]::before,
table.mapSchedule td[day=ЧТ]::before,
table.mapSchedule td[day=ПТ]::before,
table.mapSchedule td[day=СБ]::before,
table.mapSchedule td[day=ВС]::before {
  background-color: rgb(108, 177, 21);
}

.show-all-array li.fav {
