  order: 0;
}

.show-all-array li .toggle-fav {
  transition: 0.3s;
}

.show-all-array li .toggle-fav:not(.fav-active) {
  color: gray;
  opacity: 0.6;
}

.show-all-array li .toggle-fav:not(.fav-active):hover {
  color: #004785;
  opacity: 1;
}

.show-all-array li .toggle-fav.fav-active:hover {
  color: gray;
  opacity: 0.6;
