  display: flex;
  width: 100%;
  align-items: center;
  justify-content: space-between;
}

.header-has-btn a.btn-like {
  margin-bottom: 32px;
}

.show-all-array__view {
  display: none;
}

.scroll2top {
  display: none;
  width: 40px;
  height: 40px;
  position: fixed;
  right: 16px;
  bottom: 16px;
  border-radius: 50%;
  background: #ccc;
  color: #fff;
  cursor: pointer;
  line-height: 40px;
  text-align: center;
  transition: 0.3s;
  opacity: 0.7;
  z-index: 10020;
}

.scroll2top:hover {
  background-color: #3887be;
}

/* FILTER CHECKBOXES  */
.toggler-wrapper {
  display: block;
  width: 100%;
  cursor: pointer;
  position: relative;
}

.toggler-wrapper input[type=checkbox] {
  display: none;
}

.toggler-wrapper input[type=checkbox]:checked + .toggler-slider {
  background-color: #3887be;
  /*background-color: #44cc66;*/
}

.toggler-wrapper .toggler-slider {
  background-color: #ccc;
  position: absolute;
  border-radius: 100px;
  top: 0;
  left: 0;
  width: 38px;
  height: 20px;
  -webkit-transition: all 300ms ease;
  transition: all 300ms ease;
}

.toggler-wrapper .toggler-knob {
  position: absolute;
  -webkit-transition: all 300ms ease;
  transition: all 300ms ease;
}

/*Style 1*/
.toggler-wrapper.style-1 input[type=checkbox]:checked + .toggler-slider .toggler-knob {
  left: calc(100% - 16px - 2px);
}

.toggler-wrapper.style-1 input[type=checkbox]:checked + .toggler-slider + .label-like {
  color: #3887be;
}

.toggler-wrapper.style-1 .toggler-knob {
  width: 16px;
  height: 16px;
  border-radius: 50%;
  left: 2px;
  top: 2px;
  background-color: #fff;
}

.label-like {
  margin-left: 48px;
  /*padding-top: 1px;*/
}

.toggler-wrapper:hover .label-like {
  text-decoration: underline;
}

