.group__list {
  width: 300px;
  max-width: 300px;
  min-width: 300px;
  padding: 0 24px 40px;
  max-height: calc(100dvh - 60px);
  overflow-y: auto;
}
.group__list ul {
  padding: 0;
  margin: 0;
  list-style-type: none;
}
.group__list ul li a {
  position: relative;
  color: black;
}
.group__list ul li a:active, .group__list ul li a:visited {
  color: black;
}
.group__list ul li a::before {
  position: absolute;
  left: -8px;
  height: 100%;
  width: 2px;
  top: 0;
  content: "";
  display: block;
  background-color: none;
